.recovery__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.recovery__title {
    text-align: center;
    margin: 30px 0 50px;
}

.recovery__input {
    width: 300px;
}

.recovery__text {
    margin-top: 30px;
    width: 300px;
}

.recovery__error {
    text-align: center;
    min-height: 1em;
    width: 300px;
    color: #fd6d6d;
}

.recovery__buttons {
    margin-top: 80px;
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.recovery__login_box {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}
